function initParalax() {
    const intro = document.querySelector('.intro');
    const moon = document.querySelector('.moon');
    const ninja = document.querySelector('.ninja');
    const dots3 = document.querySelector('.dots3');
    const dots4 = document.querySelector('.dots4');
    const grass = document.querySelector('.grass');
    const cloud1 = document.querySelector('.cloud1');
    const cloud2 = document.querySelector('.cloud2');

    const index = 5;
    const speed = 0.01;

    let positionX = 0, positionY = 0;
    let coordXprocent = 0, coordYprocent = 0;

    function setMouseParallaxStyle() {
        const distX = coordXprocent - positionX;
        const distY = coordYprocent - positionY;

        positionX = positionX + (distX * speed);
        positionY = positionY + (distY * speed);

        moon.style.cssText = `transform: translate(${positionX / index}%, ${positionY / 9}%) rotate(-5.6deg)`;
        ninja.style.cssText = `transform: translate(${-(positionX / index)}%, ${-(positionY / index)}%)`;
        dots3.style.cssText = `transform: translate(${(positionX / index)}%, ${(positionY / index)}%) rotate(-15.01deg)`;
        dots4.style.cssText = `transform: translate(${-(positionX / 6)}%, ${-(positionY / 6)}%)`;
        cloud1.style.cssText = `transform: translate(${(positionX / 3)}%, ${(positionY / 3)}%)`;
        cloud2.style.cssText = `transform: translate(${(positionX / 3)}%, ${(positionY / 3)}%)`;
        grass.style.cssText = `transform: translate(${-(positionX / 12)}%, ${-(positionY / 10)}%)`;

        requestAnimationFrame(setMouseParallaxStyle);
    }

    setMouseParallaxStyle();

    intro.addEventListener('mousemove', (e) => {
        const pageWidth = intro.offsetWidth;
        const pageHeight = intro.offsetHeight;

        const coordX = e.pageX - pageWidth / 2;
        const coordY = e.pageY - pageHeight / 2;

        coordXprocent = coordX / pageWidth * 100;
        coordYprocent = coordY / pageHeight * 300;
    });
}

if(window.innerWidth >= 1024) {
    initParalax();
}